
.footer-div{
    display:flex;
    flex-direction: column;
    width: 100%;  
    margin-top: 27vh;         
    p{
        font-size: 1.3em;
        text-align: center;
        margin: 0;
    }
}
.footer-div-basic{
    display: flex;
    flex-direction: column;    
    align-items: center;
    margin-bottom: 2em;
    
    p{
        font-size: 1.3em;
        text-align: center;
        margin: 0;
    }
}

@media(max-width: 1028px){    
    .footer-div{
        margin-top: 20vh;
        bottom: 2px;
        
    }
    
}
@media(max-width: 768px){
    .footer-div{
        margin-top: 17vh;
        p{
            margin-bottom: 1em;
        }                
    }
    
}