.modal-container{
    display:flex;
    flex-direction: row;
    justify-content: space-between;    
    gap: 2em;
    background: black;  
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    z-index: 1000;
  }
  .close-btn{
    position: fixed;
    right: 25px;
    top: 20px;
    height: 2em;
    color: white;
    background: black;
    border: 3px solid white;
    box-shadow: 1px 1px 10px wheat;
    border-radius: 0.2em;
    transition: transform 1000ms ease-in-out;
    transform: scale(1);
    font-size: 0.9em;
  }
  .close-btn:hover{
    transform: scale(1.1);
  }
  .preview-btn{
    height: 4em;    
    border-radius: 1em 0 0 1em;
    position:relative;
    top: 33%;
    left: 0.2em;
    background: transparent;     
    font-size: 5em; 
    border: 3px solid white;
    box-shadow: 1px 1px 15px wheat;       
    transition: transform 400ms ease-in-out;
    padding: 0.1em;  
    cursor: pointer;     
  }
  .preview-btn:hover{
    background: rgb(31, 31, 31);
  }
  .next-btn{
    height: 4em;    
    border-radius: 0 1em 1em 0;
    position:relative;
    top: 33%;
    right: 0.2em;
    background: transparent;     
    font-size: 5em; 
    border: 3px solid white;
    box-shadow: 1px 1px 15px wheat;       
    transition: transform 400ms ease-in-out;
    padding: 0.1em; 
    cursor: pointer;         
  }
  .next-btn:hover{
    background: rgb(31, 31, 31);        
  }
.img-div{    
    object-fit: cover;
    width: 1700px;
}
.img-modal{
    width: 100%;
    margin-top: 3.5em;
}
.chevron-icon{
  text-shadow: 2px 2px 2px grey;
  color: white;   
}
.open{
  display:flex;  
}

.close{
  display: none;  
}

@media (max-width: 1024px){
  .img-modal{
    margin-top: 5em;
  }
  .next-btn{
    height: 2em;
    top:13%
  }
  .preview-btn{
    height: 2em;
    top:13%
  }
  .close-btn{
    height: 3em;
    width: 5em;
  }
}
@media (max-width: 768px){
  .img-modal{
    margin-top: 5em;
    width:20em;
  }
  .next-btn{    
    top:10%;
    border:none;
    box-shadow: none;
    right: 0.5em;
  }
  .next-btn:hover{
    background: transparent;
  }
  .preview-btn{    
    top:10%;
    border:none;
    box-shadow: none;
    left: 0.5em;
  }.preview-btn:hover{
    background: transparent;
  }
  .chevron-icon{    
    z-index: 1000;
    font-size: 0.5em;
  }  
  .open{
    display:flex;
    justify-content: center;  
  }
  .close-btn{
    width: 4.5em;
    height: 2em;
  }
}