@import '../../variables.scss';

.form-div{
    display: flex;
    flex-direction: column;    
    align-items: center;
    //margin-bottom: 10vh;
    h2{
        margin-top: 2em;    
        font-weight: 300;    
    }
    .contact-form{
        display: flex;
        flex-direction: column;        
        align-items: center;       
        padding: 1em;                
        width: 45%;
        .form-input{
            border-radius: 0.5em;
            padding: 10px;
            border: none;
            width: 90%;
            font-size: 1.2em;
            font-family: monospace;
            margin-bottom: 2.5em;
            box-shadow: 0 0.5rem 1rem rgba(0,0,0,.4);
            background-image: $primary-azur-color;
            
        }
        .form-textarea{
            padding: 10px;
            border: none;
            height: 9em;
            width: 90%;
            border-radius: 0.5em;
            font-size: 1.2em;
            margin-bottom: 2.5em;
            box-shadow: 0 0.5rem 1rem rgba(0,0,0,.4);
            background: $primary-azur-color;          
            
        }.form-button{            
            padding: 0.5em 2em 0.5em 2em;            
            border-radius: 2em;
            font-size: 1.3em;
            box-shadow: 0 0.5rem 1rem rgba(0,0,0,.4);
            background-image: $primary-azur-color; 
            border: 4px double grey;
            color: white;
            cursor: pointer;  
            text-shadow: 1px 1px 4px black; 
            transition: 500ms ease-out; 
            transform: translateY(0%);    
        } 
        .form-button:hover{
            transform: translateY(-5%);            
            border-color: white;
        }
    }
}

@media(max-width: 1024px) {
    .form-div{
        .contact-form{
            width: 80%;                        
        }
        h2{
            margin-top: 0.5em;
        }
    }    
}

@media(max-width: 768px) {
    .form-div{
        .contact-form{
            width:95%;                        
        }
    }    
}