.portfolio-section{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5em;
    
}

.div-box{    
    display: flex;
    justify-content: center;
    align-items: center;    
    row-gap: 2.2em;  
    column-gap: 6em; 
    flex-direction: row;
    flex-wrap: wrap;
}



@media (max-width: 1024px){
    
    .portfolio-section{
        padding: 0.2em;
    }
    .div-box{        
        margin-bottom: 2em;
        margin-top: 2%;
        width: 100%;
        gap: 2em;
        margin-bottom: 4em;
    }
}
@media (max-width: 768px){
    .div-box{        
        margin-bottom: 2em;
        margin-top: 7%;
        gap: 1.5em;        
        width: 100%;      
    }
    .div-box-before{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    
    
    
}